import React, { useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { IconButton, FormHelperText } from "@material-ui/core";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import CustomInput from "components/CustomInput/CustomInput.js";
import CustomSelect from "components/CustomSelect/CustomSelect.js";
import AddButton from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardAvatar from "components/Card/CardAvatar.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";

import avatar from "assets/img/faces/marc.jpg";
import Subcategory from "views/Subcategory/Subcategory";
import back from 'assets/img/back.png'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import { NativeSelect, Select, FormControl, InputLabel } from '@material-ui/core';
import FormInput from "components/FormInput/FormInput.js";
import { postData1, getData } from "components/FetchServices";

const styles = {
  formControl: {
    marginTop: '8%',
    width: '100%',
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0"
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none"
  },
  cardButtonWhite: {
    color: "black",
    backgroundColor: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    '&:hover': {
      backgroundColor: "#FFFFFF",
      color: "black"
    },
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1"
    }
  },
  underline: {
    "&:hover:not($disabled):before,&:before": {
      borderColor: "#AAAAAA" + " !important",
      borderWidth: "1px !important",

    },
    "&:after": {
      borderColor: "#972C26"
    }
  },
};


const useStyles = makeStyles(styles);

export default function AddSubcat() {
  const classes = useStyles();
  const [form, setForm] = React.useState(true)
  const [table, setTable] = React.useState(false)
  const [dropdown, setDropdown] = React.useState([])
  const [name, setName] = React.useState('')
  const [category, setCategory] = React.useState('');
  const [subCategoryError, setSubCategoryError] = React.useState(false);
  const [categoryError, setCategoryError] = React.useState(false);
  const [nameArabic, setNameArabic] = React.useState('')
  const [nameArabicError, setNameArabicError] = React.useState(false);

  const changeView = () => {
    setTable(true)
    setForm(false)
  }

  const categoryDropdown = async () => {

    var body = {
      "language": "en",
    }

    let result = await postData1('category-list', body)
    console.log(result)
    if (result.status == true) {
      setDropdown(result.data)
    } else {
      setDropdown([])
    }
  }

  useEffect(() => {
    categoryDropdown()
  }, [])

  

  const addSubcategory = async () => {

    if(name == ""){
      setSubCategoryError(true)
    }

    if(category == ""){
      setCategoryError(true)
    }

    if(nameArabic == ""){
      setNameArabicError(true)
    }

    var body = {
      "name": name,
      "category_id": category,
      "name_arbic": nameArabic,
    }

    let result = await postData1('sub-category-add', body)

    if (result.status == true) {
      clearValues()
      changeView()
    } else {
      // alert('false')
    }

  }

  const clearValues = () => {
    setName('')
    setCategory('')
  }


  return (
    console.log(dropdown),
    <div>
      <GridContainer>
        {form &&
          <GridItem xs={12} sm={12} md={8}>
            <Card>
              <CardHeader color="primary">
                <GridContainer>
                  <GridItem xs={3} md={5}>
                    <IconButton style={{ padding: '0px' }} aria-label="Edit">
                      <ArrowBackIosIcon className={classes.cardTitleWhite} onClick={changeView} />
                    </IconButton>
                  </GridItem>
                  <GridItem xs={8} md={4}><h4 className={classes.cardTitleWhite}>Add Subcategory</h4></GridItem>
                </GridContainer>
              </CardHeader>
              <CardBody>
                <GridContainer>

                  <GridItem xs={12} sm={12} md={6}>
                    <FormControl className={classes.formControl}>
                      <InputLabel htmlFor="age-native-simple">Category</InputLabel>
                      <Select native value={category} onChange={(event) => setCategory(event.target.value)}>
                        <option value="" disabled></option>
                        {dropdown.map(item =>
                        (
                          <option value={item.id}>{item.name}</option>
                        )

                        )}
                      </Select>
                    </FormControl>
                      {categoryError && <FormHelperText error={categoryError}>Please select a category name</FormHelperText>}
                  </GridItem>
                  <GridItem xs={12} sm={12} md={6}>
                    <FormInput
                      type="text"
                      label="Name"
                      id="name"
                      value={name}
                      onChange={(event) => setName(event.target.value)}
                    />
                    {subCategoryError && <FormHelperText error={subCategoryError}>Please enter the subcategory name</FormHelperText>}
                  </GridItem>
                </GridContainer>
                <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                    <FormInput
                      type="text"
                      label="Arabic Name"
                      id="name_arabic"
                      value={nameArabic}
                      onChange={(event) => setNameArabic(event.target.value)}
                    />
                    {nameArabicError && <FormHelperText error={nameArabicError}>Please enter the subcategory name in Arabic</FormHelperText>}
                  </GridItem>
                </GridContainer>
              </CardBody>
              <CardFooter>
                <AddButton color="primary" onClick={addSubcategory}>Add</AddButton>
              </CardFooter>
            </Card>
          </GridItem>
        }{table && <GridItem xs={12} sm={12} md={12}><Subcategory /></GridItem>}
      </GridContainer>
    </div>
  );

}
