import React, { forwardRef } from "react";

import classNames from "classnames";
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Input from "@material-ui/core/Input";
// @material-ui/icons

import styles from "assets/jss/material-dashboard-react/components/customInputStyle.js";

const useStyles = makeStyles(styles);

const FormInput = forwardRef(({ name, id, type, label, className, ...props }, ref) => {

    const classes = useStyles();

  
      const underlineClasses = classNames({
       
        [classes.underline]: true
      });
      const marginTop = classNames({
        [classes.marginTop]: label === undefined
      });


    return (

        <FormControl
        className={classes.formControl} style={{width:'100%'}}>
      
          <InputLabel
            className={classes.labelRoot}
            htmlFor={id}
          >
            {label}
          </InputLabel>
     
        <Input
          classes={{
            root: marginTop,
            disabled: classes.disabled,
            underline: underlineClasses
          }}
          type={type} id={id} name={name} {...props}
        />
       
      </FormControl>

        // <div className="form-group">            
        //     <input type={type} className={`form-control ${className}`} id={id} name={name} {...props} />
        //     {label && <label className="animLabel" htmlFor={id}>{label}</label>}
        // </div>
    )
})
export default FormInput;