import React, {useEffect} from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import { Button, TableCell, TableRow, TableBody, TableHead, Table, Avatar, Select, FormControl, InputLabel } from "@material-ui/core";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
//import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import AddSubcat from '../Subcategory/AddSubcat';
import AddIcon from '@material-ui/icons/Add';
import EditSubcat from './EditSubcat';
import Image from "assets/img/food-2.jpg";
import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import IconButton from "@material-ui/core/IconButton";

import {
  defaultFont,
  primaryColor,
  dangerColor,
  grayColor
} from "assets/jss/material-dashboard-react.js";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { postData1, getData } from "components/FetchServices";

// const styles = {

// };

const useStyles = makeStyles(styles);

export default function SubCategory(props) {
  const classes = useStyles();
  const { tableHead, tableData, tableHeaderColor } = props;
  const [table, setTable] = React.useState(true);
  const [form, setForm] = React.useState(false);
  const [subCategoryList, setSubCategoryList] = React.useState([]);
  const [categoryDropdown, setCategoryDropdown] = React.useState([])
  const [categoryId, setCategoryId] = React.useState(categoryDropdown[0]?.id)
  

  const changeView = () => {
    setTable(false)
    setForm(true)
  }

  const categoryDropdownList = async () => {

    var body = {
      "language": "en",
    }

    let result = await postData1('category-list', body)
    if (result.status == true) {
      setCategoryDropdown(result.data)
    } else {
      setCategoryDropdown([])
    }
  }

  const subCategoryData = async (categoryId) => {

    console.log("oooooooo",categoryId)

    if(categoryId){
      var body = {
        "category_id": categoryId,
        "language": "en",
      }
  
      let result = await postData1('sub-category-list', body)
      if (result.status == true) {
        setSubCategoryList(result.data)
      } else {
        setSubCategoryList([])
      }
    }
  }

  useEffect(() => {
    categoryDropdownList();
    subCategoryData(categoryId);
  }, [categoryId])


  return (
    <GridContainer>
      {table &&

        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <GridContainer>
                <GridItem xs={8} sm={10} md={11}>
                  <h4 className={classes.cardTitleWhite}>Subcategory</h4>
                </GridItem>
                <GridItem xs={4} sm={2} md={1}><Button style={{ borderBottomLeftRadius: '16px', borderTopLeftRadius: '16px' }}
                  className={classes.cardButtonWhite} startIcon={<AddIcon />}
                  onClick={changeView}>Add</Button></GridItem>
              </GridContainer>
            </CardHeader>
            <CardBody>
              <GridContainer>
                <GridItem xs={12} sm={12} md={6}>
                  <FormControl className={classes.formControl}>
                    
                    <Select native value={categoryId} onChange={(event) => setCategoryId(event.target.value)}>
                      <option defaultValue={categoryDropdown[0]?.id} >{categoryDropdown[0]?.name}</option>
                      {categoryDropdown.map((item) =>{
                      console.log("hiiiiiiii",item)
                       return (
                        <option value={item.id}>{item.name} </option>
                      )
                      }
                      )}
                    </Select>
                  </FormControl>
                </GridItem>
              </GridContainer>
              <div className={classes.tableResponsive}>
                <Table className={classes.table}>

                  <TableHead className={classes[tableHeaderColor + "TableHeader"]}>
                    <TableRow className={classes.tableHeadRow}>
                      <TableCell className={classes.tableCell + " " + classes.tableHeadCell}>Image
                      </TableCell>
                      <TableCell className={classes.tableCell + " " + classes.tableHeadCell}>Category
                      </TableCell>
                     <TableCell className={classes.tableCell + " " + classes.tableHeadCell}>Subcategory
                      </TableCell>
                      <TableCell className={classes.tableCell + " " + classes.tableHeadCell}>Action
                      </TableCell>
                    </TableRow>
                  </TableHead>
                  {subCategoryList.map(item => {
                    return (
                      <TableBody>
                        <TableRow className={classes.tableBodyRow}>
                          <TableCell className={classes.tableCell}>
                            <Avatar alt="Remy Sharp" src={Image} />
                          </TableCell>
                          <TableCell className={classes.tableCell} >{item.category_name} </TableCell>
                          <TableCell className={classes.tableCell} >{item.name} </TableCell>
                          <TableCell className={classes.tableActions}>
                            <IconButton
                              aria-label="Edit"
                              className={classes.tableActionButton}
                            >
                              <Edit className={classes.tableActionButtonIcon + " " + classes.edit}
                              />
                            </IconButton>
                            <IconButton
                              aria-label="Close"
                              className={classes.tableActionButton}
                            >
                              <Close
                                className={
                                  classes.tableActionButtonIcon + " " + classes.close
                                }
                              />
                            </IconButton>
                          </TableCell>
                        </TableRow>

                      </TableBody>
                    )
                  })}
                </Table>
              </div>
            </CardBody>
          </Card>
        </GridItem>
      }
      {form && <GridItem xs={12} sm={12} md={12}>
        <AddSubcat />
      </GridItem>}

    </GridContainer>
  );
}
