import React, { useEffect } from "react";
import { makeStyles } from "@material-ui/core/styles";
import {
  IconButton,
  InputLabel,
  FormControl,
  Select,
} from "@material-ui/core";
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import AddButton from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import { Paper, Button } from "@material-ui/core";
import Menu from "views/Menu/Menu";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import { AddCircle, Delete } from "@material-ui/icons";
import FormInput from "components/FormInput/FormInput.js";
import { postData1, postDataAndImage } from "components/FetchServices";

const styles = {
  centerAlign: {
    display : "flex",
    alignItems : "center",
    justifyContent : "center"
  },
  formControl: {
    width: "100%",
    marginTop: "8%",
  },
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  cardButtonWhite: {
    color: "black",
    backgroundColor: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "&:hover": {
      backgroundColor: "#FFFFFF",
      color: "black",
    },
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
  underline: {
    "&:hover:not($disabled):before,&:before": {
      borderColor: "#AAAAAA" + " !important",
      borderWidth: "1px !important",
    },
    "&:after": {
      borderColor: "#972C26",
    },
  },
  select: {
    paddingTop: "24px",
  }
};

const useStyles = makeStyles(styles);

export default function AddMenu(props) {
  const classes = useStyles();
  const [form, setForm] = React.useState(true);
  const [table, setTable] = React.useState(false);
  const [dropdown, setDropdown] = React.useState([]);
  const [subdropdown, setSubDropdown] = React.useState([]);
  const [name, setName] = React.useState("");
  const [nameArabic, setNameArabic] = React.useState('');
  const [descriptionArabic, setDescriptionArabic] = React.useState('');
  const [price, setPrice] = React.useState("");
  const [category, setCategory] = React.useState("");
  const [subcategory, setSubCategory] = React.useState("");
  const [getPicture, setGetPicture] = React.useState("");
  const [calorie, setCalorie] = React.useState("");
  const [description, setDescription] = React.useState("");
  const [menuType, setMenuType] = React.useState("normal");
  const [addonCategoryName, setAddonCategoryName] = React.useState([""]);
  const [addonCategoryNameArabic, setAddonCategoryNameArabic] = React.useState([""]);
  const [addonCategoryType, setAddonCategoryType] = React.useState([""]);
  const [addonCategoryMaxLimit, setAddonCategoryMaxLimit] = React.useState([0]);
  const [addonItemName, setAddonItemName] = React.useState([[""]]);
  const [addonItemNameArabic, setAddonItemNameArabic] = React.useState([[""]]);
  const [addonItemCalorie, setAddonItemCalorie] = React.useState([[0]]);
  const [addonItemPrice, setAddonItemPrice] = React.useState([[0]]);

  useEffect(() => {
    categoryDropdown();
  }, []);

  const changeView = () => {
    setTable(true);
    setForm(false);
  };

  const categoryDropdown = async () => {
    var body = {
      language: "en",
    };

    let result = await postData1("category-list", body);
    if (result.status == true) {
      setDropdown(result.data);
    } else {
      setDropdown([]);
    }
  };

  const subDropdown = async (id) => {
    var body = {
      category_id: id,
      language: "en",
    };

    let result = await postData1("sub-category-list", body);
    if (result.status == true) {
      setSubDropdown(result.data);
    } else {
      setSubDropdown("");
    }
  };

  const handleChangeCategory = (event) => {
    setCategory(event.target.value);
    subDropdown(event.target.value);
  };

  const handlePicture = (event) => {
    setGetPicture(event.target.files[0]);
  };

  const handleAddDeleteClick = (isCategory, isAdd, categoryElementIndex, itemIndex) => {
    let data = [];
    if(isAdd) {
      if(isCategory) {
        data = addonCategoryName;
        data.push("");
        setAddonCategoryName(data);
        data = addonCategoryNameArabic;
        data.push("");
        setAddonCategoryNameArabic([...data]);
        data = addonCategoryType;
        data.push("");
        setAddonCategoryType([...data]);
        data = addonCategoryMaxLimit;
        data.push(0);
        setAddonCategoryMaxLimit([...data]);
        data = addonItemName;
        data.push([""]);
        setAddonItemName([...data]);
        data = addonItemNameArabic;
        data.push([""]);
        setAddonItemNameArabic([...data]);
        data = addonItemCalorie;
        data.push([0]);
        setAddonItemCalorie([...data]);
        data = addonItemPrice;
        data.push([0]);
        setAddonItemPrice([...data]);
      } else {
        data = addonItemName;
        data[categoryElementIndex].push([""]);
        setAddonItemName([...data]);
        data = addonItemNameArabic;
        data[categoryElementIndex].push([""]);
        setAddonItemNameArabic([...data]);
        data = addonItemCalorie;
        data[categoryElementIndex].push([0]);
        setAddonItemCalorie([...data]);
        data = addonItemPrice;
        data[categoryElementIndex].push([0]);
        setAddonItemPrice([...data]);
      }
    } else {
      if(isCategory) {
        data = addonCategoryName;
        data.splice(categoryElementIndex, 1);
        setAddonCategoryName([...data]);
        data = addonCategoryNameArabic;
        data.splice(categoryElementIndex, 1);
        setAddonCategoryNameArabic([...data]);
        data = addonCategoryType;
        data.splice(categoryElementIndex, 1);
        setAddonCategoryType([...data]);
        data = addonCategoryMaxLimit;
        data.splice(categoryElementIndex, 1);
        setAddonCategoryMaxLimit([...data]);
        data = addonItemName;
        data.splice(categoryElementIndex, 1);
        setAddonItemName([...data]);
        data = addonItemNameArabic;
        data.splice(categoryElementIndex, 1);
        setAddonItemNameArabic([...data]);
        data = addonItemCalorie;
        data.splice(categoryElementIndex, 1);
        setAddonItemCalorie([...data]);
        data = addonItemPrice;
        data.splice(categoryElementIndex, 1);
        setAddonItemPrice([...data]);
      } else {
        data = addonItemName;
        data[categoryElementIndex].splice(itemIndex, 1);
        setAddonItemName([...data]);
        data = addonItemNameArabic;
        data[categoryElementIndex].splice(itemIndex, 1);
        setAddonItemNameArabic([...data]);
        data = addonItemCalorie;
        data[categoryElementIndex].splice(itemIndex, 1);
        setAddonItemCalorie([...data]);
        data = addonItemPrice;
        data[categoryElementIndex].splice(itemIndex, 1);
        setAddonItemPrice([...data]);
      }
    }
  };


  let rec = JSON.parse(localStorage.getItem("LOGIN"));
  let token = rec.token;

  const addMenu = async () => {
    const formData = new FormData();

    if (menuType == "normal") {
      formData.append("name", name);
      formData.append("name_arbic", nameArabic);
      formData.append("menu_sub_category_id", subcategory);
      formData.append("price", price);
      formData.append("calorie", calorie);
      formData.append("description", description);
      formData.append("description_arbic", descriptionArabic);
      formData.append("image", getPicture);
      formData.append("type", menuType);
    } else {
      formData.append("name", name);
      formData.append("name_arbic", nameArabic);
      formData.append("menu_sub_category_id", subcategory);
      formData.append("price", price);
      formData.append("calorie", calorie);
      formData.append("description", description);
      formData.append("description_arbic", descriptionArabic);
      formData.append("image", getPicture);
      formData.append("type", menuType);
      addonCategoryName.forEach((item, index)=> formData.append(`addon_category_name[${index}]`, item));
      addonCategoryNameArabic.forEach((item, index)=> formData.append(`addon_category_name_arbic[${index}]`, item));
      addonCategoryType.forEach((item, index)=> formData.append(`addon_category_type[${index}]`, item));
      addonCategoryMaxLimit.forEach((item, index)=> formData.append(`addon_category_max_limit[${index}]`, item));
      addonItemName.forEach((category, categoryIndex)=> category.forEach((item, itemIndex) => formData.append(`addon_item_name[${categoryIndex}][${itemIndex}]`, item)));
      addonItemNameArabic.forEach((category, categoryIndex)=> category.forEach((item, itemIndex) => formData.append(`addon_item_name_arbic[${categoryIndex}][${itemIndex}]`, item)));
      addonItemPrice.forEach((category, categoryIndex)=> category.forEach((item, itemIndex) => formData.append(`addon_item_price[${categoryIndex}][${itemIndex}]`, item)));
      addonItemCalorie.forEach((category, categoryIndex)=> category.forEach((item, itemIndex) => formData.append(`addon_item_calorie[${categoryIndex}][${itemIndex}]`, item)));
    }
    const config = {
      headers: {
        token: token,
        "content-type": "multipart/form-data",
      },
    };
    const result = await postDataAndImage("menu-add", formData, config);
    if(result && result.status) {
      props.handleUpload(true);
    } else {
      props.handleUpload(false)
    }
  };

  const customStyles = {
    option: (provided, state) => ({
      ...provided,
      // color: "white",
      width: state.selectProps.width,
      padding: 10,
    }),
    control: () => ({
      display: "none",
      width: 100,
    }),
  };

  const clearValues = async () => {
    setName("");
    setPrice("");
    setSubCategory("");
    setCategory("");
    setGetPicture("");
    setDescription("");
    setCalorie("");
    setMenuType("");
  };

  const handleCustomMenuChanges = (value, categoryIndex, categoryElementIndex, itemIndex, itemElementIndex) => {
    let data = [];
    if(itemIndex !== null) {
      if(itemIndex === 0) {
        data = addonItemName;
        data[categoryElementIndex][itemElementIndex] = value;
        setAddonItemName([...data]);
      } else if(itemIndex === 1) {
        data = addonItemNameArabic;
        data[categoryElementIndex][itemElementIndex] = value;
        setAddonItemNameArabic([...data]);
      } else if(itemIndex === 2) {
        data = addonItemPrice;
        data[categoryElementIndex][itemElementIndex] = value;
        setAddonItemPrice([...data]);
      } else if(itemIndex === 3) {
        data = addonItemCalorie;
        data[categoryElementIndex][itemElementIndex] = value;
        setAddonItemCalorie([...data]);
      }
    } else {
      if(categoryIndex === 0) {
        data = addonCategoryName;
        data[categoryElementIndex] = value;
        setAddonCategoryName([...data])
      } else if(categoryIndex === 1) {
        data = addonCategoryNameArabic;
        data[categoryElementIndex] = value;
        setAddonCategoryNameArabic([...data])
      } else if(categoryIndex === 2) {
        data = addonCategoryType;
        data[categoryElementIndex] = value;
        setAddonCategoryType([...data])
      } else if(categoryIndex === 3) {
        data = addonCategoryMaxLimit;
        data[categoryElementIndex] = value;
        setAddonCategoryMaxLimit([...data])
      }
    }
  }

  return (
    (
        <GridContainer style={styles.centerAlign}>
          {form && (
            <GridItem xs={12} sm={12} md={8}>
              <Card>
                <CardHeader color="primary">
                  <GridContainer>
                    <GridItem xs={3} md={5}>
                      <IconButton style={{ padding: "0px" }} aria-label="Edit">
                        <ArrowBackIosIcon
                          className={classes.cardTitleWhite}
                          onClick={changeView}
                        />
                      </IconButton>
                    </GridItem>
                    <GridItem xs={8} md={4}>
                      <h4 className={classes.cardTitleWhite}>Add Menu</h4>
                    </GridItem>
                  </GridContainer>
                </CardHeader>
                <CardBody>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="age-native-simple">
                          Category
                        </InputLabel>
                        <Select
                          native
                          value={category}
                          onChange={(event) => handleChangeCategory(event)}
                        >
                          <option aria-label="None" value="" />
                          {dropdown.map((item) => (
                            <option value={item.id}>{item.name}</option>
                          ))}
                        </Select>
                      </FormControl>
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormControl className={classes.formControl}>
                        <InputLabel htmlFor="age-native-simple">
                          Subcategory
                        </InputLabel>
                        <Select
                          native
                          value={subcategory}
                          onChange={(event) =>
                            setSubCategory(event.target.value)
                          }
                          disabled={category ? false : true}
                        >
                          <option aria-label="None" value="" />
                          {subdropdown.map((item) => (
                            <option value={item.id}>{item.name}</option>
                          ))}
                        </Select>
                      </FormControl>
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormInput
                        type="text"
                        label="Name"
                        id="name"
                        value={name}
                        onChange={(event) => setName(event.target.value)}
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormInput
                        type="text"
                        label="Arabic Name"
                        id="nameArabic"
                        value={nameArabic}
                        onChange={(event) => setNameArabic(event.target.value)}
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={12}>
                      <FormInput
                        type="text"
                        label="Description"
                        id="description"
                        value={description}
                        onChange={(event) => setDescription(event.target.value)}
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={12}>
                      <FormInput
                        type="text"
                        label="Description Arabic"
                        id="descriptionArabic"
                        value={descriptionArabic}
                        onChange={(event) => setDescriptionArabic(event.target.value)}
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormInput
                        type="number"
                        label="Price"
                        id="price"
                        value={price}
                        onChange={(event) => setPrice(event.target.value)}
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormInput
                        style={{ marginTop: "6%" }}
                        className={classes.underline}
                        required
                        fullWidth
                        onChange={(event) => handlePicture(event)}
                        // value={getPicture}
                        // onChange={(event)=>setPicture({icon:URL.createObjectURL(event.target.files[0]),file:event.target.files[0]})}
                        type="file"
                        autoComplete="current-password"
                      />
                    </GridItem>
                  </GridContainer>
                  <GridContainer>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormInput
                        type="text"
                        label="Calorie"
                        id="calorie"
                        value={calorie}
                        onChange={(event) => setCalorie(event.target.value)}
                      />
                    </GridItem>
                    <GridItem xs={12} sm={12} md={6}>
                      <FormControl className={classes.formControl}>
                        <Select
                          className={classes.select}
                          native
                          value={menuType}
                          onChange={(event) => setMenuType(event.target.value)}
                        >
                          <option style={{display : 'none'}} aria-label="None" value="">
                            Select menu type
                          </option>
                          <option value="normal">Normal</option>
                          <option value="custom">Custom</option>
                        </Select>
                      </FormControl>
                    </GridItem>
                  </GridContainer>
                  {menuType === "custom" && (
                    addonCategoryName.map((caregory, index) => {
                      return (
                        <Paper key = {`category${index}`} variant = "outlined" style = {{padding : "8px", marginBottom : "4px"}}>
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={addonCategoryName.length > 1 ? 11 : 12}>
                              <FormInput
                                label="Addon Name"
                                onChange={(event) => handleCustomMenuChanges(event.target.value, 0, index, null, null)}
                                value={addonCategoryName[index]}
                                type="text"
                              />
                            </GridItem>
                            {addonCategoryName.length > 1 && (
                              <GridItem
                              xs={12}
                              sm={12}
                              md={1}
                              style={{ position: "relative", top: 50 }}
                            >
                              <IconButton
                                style={{ padding: "0px", color: "#bb2124" }}
                                aria-label="Edit"
                              >
                                <Delete
                                  onClick={() => handleAddDeleteClick(true, false, index, null)}
                                />
                              </IconButton>
                            </GridItem>
                            )}
                          </GridContainer>
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={12}>
                              <FormInput
                                label="Arabic Addon Name"
                                onChange={(event) => handleCustomMenuChanges(event.target.value, 1, index, null, null)}
                                value={addonCategoryNameArabic[index]}
                                type="text"
                              />
                            </GridItem>
                          </GridContainer>
                    
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={12}>
                              <FormControl className={classes.formControl}>
                                <Select
                                  className={classes.select}
                                  style = {{paddingTop : "0px"}}
                                  native
                                  onChange={(event) => handleCustomMenuChanges(event.target.value, 2, index, null, null)}
                                  value={addonCategoryType[index]}
                                >
                                  <option style={{display : 'none'}} aria-label="None" value="">
                                    Select Addon Type
                                  </option>
                                  <option value="single">Single</option>
                                  <option value="multiple">Multi-Select</option>
                                </Select>
                              </FormControl>
                            </GridItem>
                          </GridContainer>
                          <GridContainer>
                            <GridItem xs={12} sm={12} md={12}>
                              <FormInput
                                type="number"
                                label="Maximum Limit"
                                id="customPrice"
                                name="customPrice"
                                onChange={(event) => handleCustomMenuChanges(event.target.value, 3, index, null, null)}
                                value={addonCategoryMaxLimit[index]}
                              />
                            </GridItem>
                          </GridContainer>
                      {
                        addonItemName[index].map((item, itemIndex) => {
                          return (
                            <GridContainer>
                              <GridItem xs={12} sm={12} md={3}>
                                <FormInput
                                  type="text"
                                  label="Item Name"
                                  id="customName"
                                  name="customName"
                                  onChange={(event) => handleCustomMenuChanges(event.target.value, null, index, 0, itemIndex)}
                                  value={addonItemName[index][itemIndex]}
                                />
                              </GridItem>
                              <GridItem xs={12} sm={12} md={3}>
                                <FormInput
                                  type="text"
                                  label="Arabic Item Name"
                                  id="customNameArabic"
                                  name="customNameArabic"
                                  onChange={(event) => handleCustomMenuChanges(event.target.value, null, index, 1, itemIndex)}
                                  value={addonItemNameArabic[index][itemIndex]}
                                />
                              </GridItem>
                              <GridItem xs={12} sm={12} md={2}>
                                <FormInput
                                  type="number"
                                  label="Price"
                                  id="customPrice"
                                  name="customPrice"
                                  onChange={(event) => handleCustomMenuChanges(event.target.value, null, index, 2, itemIndex)}
                                  value={addonItemPrice[index][itemIndex]}
                                />
                              </GridItem>
                              <GridItem xs={12} sm={12} md={2}>
                                <FormInput
                                  type="number"
                                  label="Calorie"
                                  id="customCalorie"
                                  name="customCalorie"
                                  onChange={(event) => handleCustomMenuChanges(event.target.value, null, index, 3, itemIndex)}
                                  value={addonItemCalorie[index][itemIndex]}
                                />
                              </GridItem>
                              {addonItemName[index].length > 1 && (
                                <GridItem
                                  xs={12}
                                  sm={12}
                                  md={1}
                                  style={{ position: "relative", top: 50 }}
                                >
                                  <IconButton
                                    style={{ padding: "0px", color: "#bb2124" }}
                                    aria-label="Edit"
                                  >
                                    <Delete
                                      onClick={() => handleAddDeleteClick(false, false, index, itemIndex)}
                                    />
                                  </IconButton>
                                </GridItem>
                              )}
                              {itemIndex === addonItemName[index].length - 1 && (
                                <GridItem
                                  xs={12}
                                  sm={12}
                                  md={1}
                                  style={{ position: "relative", top: 50 }}
                                >
                                  <IconButton
                                    style={{ padding: "0px", color: "#22bb33" }}
                                    aria-label="Edit"
                                  >
                                    <AddCircle onClick={() => handleAddDeleteClick(false, true, index, itemIndex)} />
                                  </IconButton>
                                </GridItem>
                              )}
                            </GridContainer>
                          );
                        })
                      }
                      <GridContainer>
                        <GridItem
                          xs={12}
                          sm={12}
                          md={6}
                        >
                          <Button 
                            variant = "contained"
                            color="success"
                            size = "small"
                            disableElevation 
                            onClick={() => handleAddDeleteClick(true, true, null, null)}>
                              Add Another Addon
                          </Button>
                        </GridItem>
                        <GridItem
                          xs={12}
                          sm={12}
                          md={12}
                        >
                        </GridItem>
                      </GridContainer>
                    </Paper>
                      )
                    })
                  )}
                </CardBody>
                <CardFooter>
                  <AddButton style = {{margin : "auto"}} color="primary" onClick={addMenu}>
                    Save
                  </AddButton>
                  {/* <Button color="primary">Update Profile</Button> */}
                </CardFooter>
              </Card>
            </GridItem>
          )}{" "}
          {table && (
            <GridItem xs={12} sm={12} md={12}>
              <Menu />
            </GridItem>
          )}
        </GridContainer>
    )
  );
}
