import * as React from 'react';
import TextField from '@mui/material/TextField';
import { Button } from '@mui/material';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import DatePicker from '@mui/lab/DatePicker';
import MobileDatePicker from '@mui/lab/MobileDatePicker';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import Stack from '@mui/material/Stack';
import './date_picker.css'

export let val = ``

export default function DatePickers({ setState, value, fetchInitialData }) {
//   const [value, setValue] = React.useState(new Date());
  // let year, month, day; 
  // month = value.getUTCMonth() + 1; //months from 1-12
  // day = value.getUTCDate();
  // year = value.getUTCFullYear();
  

  return (
    <LocalizationProvider dateAdapter={AdapterDateFns}>
      <Stack spacing={3}>
        <MobileDatePicker
          views={['year', 'month']}
          label="sort by"
          value={value}
          onChange={(newValue) => {
            setState(p => ({...p, value: newValue, formattedDate: `${newValue?.getUTCFullYear()}-${newValue?.getUTCMonth() + 1}-${newValue.getUTCDate()}` }));
          }}
          renderInput={(params) => 
          <div className = 'date-picker-component'>
          <TextField {...params} />
          <Button variant="contained" onClick = {fetchInitialData}> Reset </Button>
          </div>
          }
        />
      </Stack>
    </LocalizationProvider>
  );
}
