const useStyles = makeStyles(styles);

const ViewOrder = ({
  match: {
    params: { id },
  },
}) => {
  const classes = useStyles();
  const [orderDetails, setOrderDetails] = useState();
  const [orderInfo, setOrderInfo] = useState();
  const [driverDetails, setDriverDetails] = useState();

  // console.log(orderInfo);

  const getOrderDetails = async () => {
    const body = {
      language: "en",
    };

    let result = await postData1(`order-detail/${id}`, body);
    if (result && result.status) {
      console.log("hit");
      setOrderInfo(result.data.order);
      setOrderDetails(result.data.order_details);
      if(result.data.driver_detail) {
        setDriverDetails(result.data.driver_detail);
      }
      if (
        result &&
        result.status &&
        result.data &&
        result.data.order &&
        result.data.order.order_status.name === "Preparing"
        // (result.data.order.order_status.name !== "Confirmed" ||
        //   result.data.order.order_status.name !== "Pending" ||
        //   result.data.order.order_status.name !== "Rejected" ||
        //   result.data.order.order_status.name !== "Delivered")
      ) {
        setInterval(() => {
          console.log("hitting");
          window.location.reload();
        }, 45000);
      }
    }
  };

  useEffect(() => {
    getOrderDetails();
  }, []);

  const handleStatusChange = async (statusId) => {
    let reqBody = {
      order_id: id,
      status_id: statusId,
    };

    let result = await postData1("order-status", reqBody);
    if (result && result.status) {
      getOrderDetails();
    }
  };

  return (
    <div>
      <GridContainer>
        <GridItem xs={12} sm={12} md={6}>
          <Card>
            <CardHead color="primary">
              <GridContainer>
                <GridItem xs={2} sm={2} md={2}>
                  {/* <ArrowBackIosIcon
                    className={classes.TableTitleWhite}
                    onClick={() => changeView()}
                  /> */}
                </GridItem>
                <GridItem xs={10} sm={10} md={10}>
                  <h4 className={classes.TableTitleWhite}>
                    Order Number : {orderInfo && orderInfo.order_no}
                  </h4>
                </GridItem>
              </GridContainer>
            </CardHead>
            <CardBody>
              <div className={classes.tableResponsive}>
                {orderDetails && (
                  <OrderDetailTable orderDetailList={orderDetails} />
                )}
              </div>
            </CardBody>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={12} md={6}>
          {orderInfo && orderInfo.order_status && orderInfo.order_status.name === "Pending" && (
            <Card>
              <CardContent>
                <Primary>Order Status</Primary>
              </CardContent>
              <CardContent>
                <Table className={classes.table}>
                  <TableHead>
                    <TableRow>
                      <TableCell>Order Status </TableCell>
                      <TableCell>Action </TableCell>
                    </TableRow>
                  </TableHead>
                  <TableBody>
                    {orderInfo && (
                      <TableRow className={classes.tableBodyRow}>
                        <TableCell className={classes.tableCell}>
                          {orderInfo && orderInfo.order_status &&orderInfo.order_status.name}
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          <Button
                            variant="contained"
                            className={classes.acceptButton}
                            onClick={() => handleStatusChange(2)}
                          >
                            Confirm
                          </Button>
                          <Button
                            variant="contained"
                            className={classes.rejectButton}
                            onClick={() => handleStatusChange(3)}
                          >
                            Reject
                          </Button>
                        </TableCell>
                      </TableRow>
                    )}
                  </TableBody>
                </Table>
              </CardContent>
            </Card>
          )}
          {orderInfo && orderInfo.order_status && orderInfo.order_status.name === "Confirmed" && (
            <Card>
              <CardHead>
                <Primary>Order Status: {orderInfo.order_status.name}</Primary>
              </CardHead>
              <CardBody className={classes.centerAlign}>
                <Button
                  variant="contained"
                  className={classes.acceptButton}
                  onClick={() => handleStatusChange(4)}
                >
                  Start Preparing
                </Button>
              </CardBody>
            </Card>
          )}

          {orderInfo && orderInfo.order_status && orderInfo.order_status.name === "Preparing" && (
            <Card>
              <CardHead>
                <Primary>Order Status: {orderInfo.order_status.name}</Primary>
              </CardHead>
              <CardBody className={classes.centerAlign}>
                <Typography className="blinking">
                  Assigning Driver...
                </Typography>
              </CardBody>
            </Card>
          )}

          {orderInfo && orderInfo.order_status && orderInfo.order_status.name === "Accepted" && (
            <Card>
              <CardHead>
                <Primary>Order Status: {orderInfo.order_status.name} </Primary>
              </CardHead>
            </Card>
          )}
        </GridItem>
      </GridContainer>

      <GridContainer>
        <GridItem xs={12} sm={6} md={3}>
          <Card className={classes.root}>
            <CardContent>
              <Primary>Customer Information</Primary>
            </CardContent>
            <CardHeader
              avatar={
                <Avatar aria-label="recipe" className={classes.avatar}>
                  {/* // {orderInfo && orderInfo.username.charAt(0).toUpperCase()} */}
                </Avatar>
              }
              // title={orderInfo && orderInfo.username.toUpperCase()}
              subheader={orderInfo && orderInfo.email}
            />

            <Divider />
            <CardContent>
              <Typography variant="body2" color="textSecondary" component="p">
                Contact : {orderInfo && orderInfo.phone}
              </Typography>
            </CardContent>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card className={classes.root}>
            <CardContent>
              <Info> Shipping Address</Info>
              <Typography variant="body2" color="textSecondary" component="p">
                {orderInfo && orderInfo.address}
              </Typography>
              <p className={classes.cardTitleWhite} variant="body2">
                {`${orderInfo && orderInfo.city}(${
                  orderInfo && orderInfo.zipcode
                }), ${orderInfo && orderInfo.state}`}
              </p>
            </CardContent>
            <Divider />
            <CardActions disableSpacing>
              <Typography variant="body2" color="textSecondary" component="p">
              {orderInfo && orderInfo.created_at}
              </Typography>
            </CardActions>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card className={classes.root}>
            <CardContent>
              <Danger> Special Instructions</Danger>
            </CardContent>

            <CardContent>
              <Typography variant="body2" color="textSecondary" component="p">
              {orderInfo && orderInfo.delivery_instructions}
              </Typography>
            </CardContent>
          </Card>
        </GridItem>
        <GridItem xs={12} sm={6} md={3}>
          <Card className={classes.root}>
            <CardContent>
              <Success>Payment Information</Success>
              <Table className={classes.table}>
                <TableHead>
                  <TableRow>
                    <TableCell>Payment Method </TableCell>
                    <TableCell>Payment Status </TableCell>
                  </TableRow>
                </TableHead>
                <TableBody>
                  <TableRow className={classes.tableBodyRow}>
                    <TableCell className={classes.tableCell}>
                      {orderInfo &&
                        orderInfo.payment_mode &&
                        orderInfo.payment_mode.name}
                    </TableCell>
                    <TableCell className={classes.tableCell}>
                      {orderInfo &&
                        orderInfo.payment_status &&
                        orderInfo.payment_status.name}
                    </TableCell>
                  </TableRow>
                </TableBody>
              </Table>
            </CardContent>
          </Card>
        </GridItem>
      </GridContainer>
    </div>
  );
};

export default withRouter(ViewOrder);
