import React, {useState, useEffect} from 'react';
import { Button, Modal } from '@material-ui/core';
import { postData1, postDataAndImage } from 'components/FetchServices';
import CardBody from 'components/Card/CardBody';
import CardHeader from 'components/Card/CardHeader';
import Card from 'components/Card/Card'
import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';
import FormInput from 'components/FormInput/FormInput';
import { Paper, IconButton,FormControl, Select } from '@material-ui/core';
import { Delete, CheckCircle, AddCircle } from '@material-ui/icons';
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import './EditMenu.css';



export default function EditMenu({menuId, handleClose}) {
    const [data, setData] = useState({});
    const [addItem, setAddItem] = useState([]);
    const [addonCategory, setAddonCategory] = useState([]);
    const [menuData, setMenuData] = useState({
      name : "",
      name_arbic : "",
      description : "",
      description_arbic : "",
      price : 0,
      calorie : 0,
      image : null
    })

    const [addon, setAddon] = useState({
      menu_id : menuId,
      addon_category_name : "",
      addon_category_name_arbic : "",
      addon_category_max_limit : 0,
      addon_category_type : "",
      addon_item_name : [""],
      addon_item_name_arbic : [""],
      addon_item_price : [0],
      addon_item_calorie : [0]
    })

    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = async() => {
        let res = await postData1('menu-detail', {menu_id :  menuId});
        if(res && res.data && res.status) {
            setData({...res.data});
            setMenuData({
              name : res.data.name,
              name_arbic: res.data.name_arbic,
              description: res.data.description,
              description_arbic : res.data.description_arbic,
              price : res.data.price,
              calorie : res.data.calorie,
              image : null
            })
            if(res.data.type === 'custom' && res.data.addon_category && res.data.addon_category.length > 0) {
              let addonData = [];
              res.data.addon_category.forEach(addon => {
                addonData.push({
                  id : addon.id,
                  name : addon.name,
                  name_arbic : addon.name_arbic,
                  max_limit : addon.max_limit,
                  type : addon.type
                })
              })
              setAddonCategory([...addonData])
            }
        } else {
            handleClose();
        }
    }

    const handleMenuChanges = (value, name) => {
      if(name === 'image') {
        if(value.files && value.files[0]) {
          setMenuData(prevData => ({
            ...prevData,
            [name] : value.files[0]
          }))
        }
      } else {
        setMenuData(prevData => ({
          ...prevData,
          [name] : value
        }))
      }
    }

    const handleEditAddonChanges = (name, value, index) => {
      let addonData = addonCategory;
      addonData[index][name] = value;
      console.log(addonData[index][name], data.addon_category[index][name])
      setAddonCategory([...addonData]);
    }

    const handleAddRemoveItem = (addon_category_id) => {
      let index = null;
      let itemData = addItem;
      addItem.forEach((item, i) => {
        if(addon_category_id === item.addon_category_id)
        index = i;
      })

      if(index !== null) {
        itemData.splice(index, 1)
      } else {
        itemData.push({
          addon_category_id : addon_category_id,
          name : "",
          name_arbic : "",
          price : 0,
          calorie : 0
        })
      }
      setAddItem([...itemData])
    }

    const handleItemChanges = (addon_category_id, name, value) => {
      let itemData = addItem;
      addItem.forEach((item, index) => {
        if(addon_category_id === item.addon_category_id) {
          itemData[index][name] = value;
        }
      });
      setAddItem([...itemData])
    }

    const submitSaveMenu = async() => {
      const formData = new FormData();
      formData.append('menu_id', data.id);
      formData.append('name', menuData.name);
      formData.append('name_arbic', menuData.name_arbic);
      formData.append('description', menuData.description);
      formData.append('description_arbic', menuData.description_arbic);
      formData.append('price', menuData.price);
      formData.append('calorie', menuData.calorie);
      formData.append('type', data.type);
      if(menuData.image !== null) {
        formData.append('image', menuData.image);
      }

      let rec = JSON.parse(localStorage.getItem("LOGIN"));
      let token = rec.token;
      const config = {
        headers: {
          token: token,
          "content-type": "multipart/form-data; charset=utf-8",
        },
      };
      let res = await postDataAndImage("menu-update", formData, config);
      if(res && res.status) {
        let res = await postData1('menu-detail', {menu_id :  data.id});
        if(res && res.data && res.status) {
            setData({...res.data});
            setMenuData({
              name : res.data.name,
              name_arbic: res.data.name_arbic,
              description: res.data.description,
              description_arbic : res.data.description_arbic,
              price : res.data.price,
              calorie : res.data.calorie
            })
        }
      }
    }
    const submitItem = async (addon_category_id) => {
      let index = null;
      addItem.forEach((item, i) => {
        if(addon_category_id === item.addon_category_id)
        index =i;
      });

      if(index !== null) {
        let res = await postData1('addon-item-add', addItem[index]);
        if(res && res.status) {
          let itemData = addItem;
          itemData.splice(index, 1);
          setAddItem([...itemData]);
          res = await postData1('menu-detail', {menu_id : data.id});
          if(res && res.data && res.status) {
            setData({...res.data});
          }
        }
      }
    }

    const deleteItem = async(id) => {
      let res = await postData1('addon-item-delete', {id : id});
      if(res && res.status) {
        res = await postData1('menu-detail', {menu_id : data.id});
        if(res && res.data && res.status) {
          setData({...res.data});
        }
      }
    }

    const deleteAddon = async(id, index) => {
      let res = await postData1('addon-category-delete', {id : id});
      if(res && res.status) {
        res = await postData1('menu-detail', {menu_id : data.id});
        if(res && res.data && res.status) {
          setData({...res.data});
          let addonData = addonCategory;
          addonData.splice(index, 1);
          setAddonCategory([...addonData]);
        }
      }
    }

    const handleAddAddonChanges = (name, value, index) => {
      let addonData = addon;
      if(name === "addon_item_name") {
        addonData.addon_item_name[index] = value;
      } else if(name === "addon_item_name_arbic") {
        addonData.addon_item_name_arbic[index] = value;
      } else if(name === "addon_item_price") {
        addonData.addon_item_price[index] = value;
      } else if(name === "addon_item_calorie") {
        addonData.addon_item_calorie[index] = value;
      } else {
        addonData[name] = value;
      } 
      setAddon({...addonData});
    }

    const handleAddonAddRemoveItem = (is_add, index) => {
      let addonData = addon;
      if(is_add) {
        addonData.addon_item_name.push("");
        addonData.addon_item_name_arbic.push("");
        addonData.addon_item_price.push(0);
        addonData.addon_item_calorie.push(0);
      } else {
        addonData.addon_item_name.splice(index, 1);
        addonData.addon_item_name_arbic.splice(index, 1);
        addonData.addon_item_price.splice(index, 1);
        addonData.addon_item_calorie.splice(index, 1);
      }
      setAddon({...addonData});
    }

    const submitAddon = async() => {
      let res = await postData1('addon-category-add', addon);
      if(res && res.status) {
        res = await postData1('menu-detail', {menu_id : data.id});
        if(res && res.data && res.status) {
          setData({...res.data});
          setAddon({
            menu_id : menuId,
            addon_category_name : "",
            addon_category_name_arbic : "",
            addon_category_max_limit : 0,
            addon_category_type : "",
            addon_item_name : [""],
            addon_item_name_arbic : [""],
            addon_item_price : [0],
            addon_item_calorie : [0]
          });
        }
      }
    }

    const submitEditAddon = async (index) => {
      console.log(index)
      let res = await postData1('addon-category-add');
    } 

    
    if(Object.keys(data).length > 0) {
        return (
            <Modal open style = {{display : "flex", alignItems : "center", justifyContent: "center"}}>
                <Card style = {{ maxWidth : "800px", marginTop : "3rem"}}>
                  <CardHeader color = "primary">
                    <GridContainer>
                      <GridItem xs={3} md={5}>
                        <IconButton style={{ padding: "0px" }} aria-label="Edit" onClick={() => handleClose()}>
                          <ArrowBackIosIcon
                          style = {{color : "#fff"}}
                          />
                        </IconButton>
                      </GridItem>
                      <GridItem xs={8} md={4}>
                        <h4 style={{margin : "0px"}}>Edit Menu</h4>
                      </GridItem>
                    </GridContainer>
                  </CardHeader>
                    <CardBody style = {{maxHeight : "calc(100vh - 7rem)", overflow : "auto"}}>
                        <GridContainer>
                            <GridItem xs={12} sm={12} md={4}>
                                <h4 style={{ fontWeight : "500", margin: "0px", fontSize: "18px"}}>Category: {data.menu_category[0].name}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={4}>
                                <h4 style={{ fontWeight : "500", margin: "0px", fontSize: "18px"}}>Subcategory: {data.menu_sub_category[0].name}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={4}>
                                <h4 style={{ fontWeight : "500", margin: "0px", fontSize: "18px"}}>Type: {data.type}</h4>
                            </GridItem>
                        </GridContainer>
                        <GridContainer>
                          <GridItem xs={12} sm={12} md={6}>
                            <FormInput
                              type="text"
                              label="Name"
                              id="name"
                              onChange ={(e) => handleMenuChanges(e.target.value, 'name')}
                              value={menuData.name}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={6}>
                            <FormInput
                              type="text"
                              label="Arabic Name"
                              id="nameArabic"
                              onChange ={(e) => handleMenuChanges(e.target.value, 'name_arbic')}
                              value={menuData.name_arbic}
                            />
                          </GridItem>
                        </GridContainer>
                        <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <FormInput
                            type="text"
                            label="Description"
                            id="description"
                            onChange ={(e) => handleMenuChanges(e.target.value, 'description')}
                            value={menuData.description}
                          />
                        </GridItem>
                      </GridContainer>
                      <GridContainer>
                        <GridItem xs={12} sm={12} md={12}>
                          <FormInput
                            type="text"
                            label="Description Arabic"
                            id="descriptionArabic"
                            onChange ={(e) => handleMenuChanges(e.target.value, 'description_arbic')}
                            value={menuData.description_arbic}
                          />
                        </GridItem>
                      </GridContainer>
                        <GridContainer>
                          <GridItem xs={12} sm={12} md={6}>
                            <FormInput
                              type="text"
                              label="Price"
                              id="price"
                              onChange ={(e) => handleMenuChanges(e.target.value, 'price')}
                              value={menuData.price}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={6}>
                            <FormInput
                              type="text"
                              label="Calorie"
                              id="calorie"
                              onChange ={(e) => handleMenuChanges(e.target.value, 'calorie')}
                              value={menuData.calorie}
                            />
                          </GridItem>
                          <GridItem xs={12} sm={12} md={12}>
                            <FormInput
                              style={{ marginTop: "6%" }}
                              required
                              fullWidth
                              // value={getPicture}
                              // onChange={(event)=>setPicture({icon:URL.createObjectURL(event.target.files[0]),file:event.target.files[0]})}
                              type="file"
                              autoComplete="current-password"
                              onChange = {(e) => handleMenuChanges(e.target, 'image')}
                            />
                          </GridItem>
                        </GridContainer>
                        {(data.description !== menuData.description || data.name !== menuData.name || parseInt(data.calorie) !== parseInt(menuData.calorie) || parseInt(data.price) !== parseInt(menuData.price) || menuData.image !== null) && (
                          <GridContainer>
                            <GridItem xs = {12} sm = {12} md = {12}>
                              <button className='save-menu-btn' onClick={() => submitSaveMenu()}>Save Menu</button>
                            </GridItem>
                          </GridContainer>
                        )}
                          {(data.type === 'custom' && addonCategory && addonCategory.length > 0) && (
                              <>
                              <h4 style={{textAlign : "center", textDecoration: "underline", fontWeight : "900", margin: "0px", fontSize: "16px", marginTop : "2rem"}}>Addons</h4>
                              {addonCategory.map((category, index)=> {
                                let flag = false;
                                return (
                                  
                                <Paper  key = {`${category.id}`} variant = "outlined" style = {{padding : "8px", marginBottom : "4px", Width : "100%"}}>
                                <div style={{marginBottom : "1rem"}}>
                                  <GridContainer>
                                    <GridItem xs={12} sm={6} md={3}>
                                      <FormInput
                                        type="text"
                                        label="Addon Name"
                                        value={category.name}
                                        onChange = {(e) => handleEditAddonChanges('name', e.target.value, index)}
                                      />
                                    </GridItem>
                                    <GridItem xs={12} sm={6} md={3}>
                                      <FormInput
                                        type="text"
                                        label="Addon Arabic Name"
                                        value={category.name_arbic}
                                        onChange = {(e) => handleEditAddonChanges('name_arbic', e.target.value, index)}
                                      />
                                    </GridItem>
                                    <GridItem xs={12} sm={6} md={3}>
                                      <FormInput
                                        type="number"
                                        label="Max Limit"
                                        value={category.max_limit}
                                        onChange = {(e) => handleEditAddonChanges('max_limit', e.target.value, index)}
                                      />
                                    </GridItem>
                                    <GridItem xs={12} sm={6} md={3}>
                                      <FormControl style = {{width : "100%", marginTop : "8%"}}>
                                        <Select
                                          style = {{paddingTop : "30px"}}
                                          native
                                          value = {category.type}
                                          onChange = {(e) => handleEditAddonChanges('type', e.target.value, index)}
                                        >
                                          <option value="single">Single</option>
                                          <option value="multiple">Multi-Select</option>
                                        </Select>
                                      </FormControl>
                                    </GridItem>
                                    {(data.addon_category[index].name !== category.name || data.addon_category[index].type !== category.type || data.addon_category[index].name_arbic !== category.name_arbic || data.addon_category[index].max_limit !== category.max_limit) && (
                                      <GridItem xs = {12} sm = {12} md = {12}>
                                        <button onClick={() => submitEditAddon(index)} className='save-menu-btn' style={{margin : "0px 0px 16px auto"}}>Save Addon</button>
                                      </GridItem>
                                    )}
                                  </GridContainer>
                                </div>
                                {(data.addon_item && data.addon_item.length > 0 && (
                                  <>
                                  <h4 style={{textAlign : "center", textDecoration: "underline", fontWeight : "900", margin: "0px", fontSize: "15px"}}>Items</h4>
                                  <table className='menu-edit-table'>
                                    <thead>
                                      <tr>
                                        <th>Name</th>
                                        <th>Arabic Name</th>
                                        <th>Price</th>
                                        <th>calorie</th>
                                        <th>Action</th>
                                      </tr>
                                    </thead>
                                    <tbody>
                                      {data.addon_item.map(item => {
                                        if(item.addon_category_id === category.id) {
                                          return (
                                            <tr>
                                              <td>{item.name}</td>
                                              <td>{item.name_arbic}</td>
                                              <td>{item.price}</td>
                                              <td>{item.calorie}</td>
                                              <td><button onClick={() => deleteItem(item.id)}>Delete</button></td>
                                            </tr>
                                          )
                                        }
                                      })}
                                    </tbody>
                                  </table>
                                  {addItem.length > 0 && (
                                    addItem.map(item => {
                                      if(item.addon_category_id === category.id) {
                                        flag = true;
                                        return (
                                          <GridContainer>
                                            <GridItem xs={12} sm={12} md={3}>
                                              <FormInput
                                                type="text"
                                                label="Item Name"
                                                id="customName"
                                                value={item.name}
                                                onChange = {(e) => handleItemChanges(item.addon_category_id, 'name', e.target.value)}
                                              />
                                            </GridItem>
                                            <GridItem xs={12} sm={12} md={3}>
                                              <FormInput
                                                type="text"
                                                label="Arabic Item Name"
                                                id="customNameArabic"
                                                value={item.name_arbic}
                                                onChange = {(e) => handleItemChanges(item.addon_category_id, 'name_arbic', e.target.value)}
                                              />
                                            </GridItem>
                                            <GridItem xs={12} sm={12} md={2}>
                                              <FormInput
                                                type="number"
                                                label="Price"
                                                id="customPrice"
                                                name="customPrice"
                                                value={item.price}
                                                onChange = {(e) => handleItemChanges(item.addon_category_id, 'price', e.target.value)}
                                              />
                                            </GridItem>
                                            <GridItem xs={12} sm={12} md={2}>
                                              <FormInput
                                                type="number"
                                                label="Calorie"
                                                id="customCalorie"
                                                name="customCalorie"
                                                value={item.calorie}
                                                onChange = {(e) => handleItemChanges(item.addon_category_id, 'calorie', e.target.value)}
                                              />
                                            </GridItem>
                                            <GridItem
                                              xs={12}
                                              sm={12}
                                              md={1}
                                              style={{ position: "relative", top: 50 }}
                                            >
                                              <IconButton
                                                style={{ padding: "0px", color: "#bb2124" }}
                                                aria-label="Edit"
                                              >
                                                <Delete onClick={() => handleAddRemoveItem(category.id)}/>
                                              </IconButton>
                                            </GridItem>
                                            <GridItem
                                              xs={12}
                                              sm={12}
                                              md={1}
                                              style={{ position: "relative", top: 50 }}
                                            >
                                              <IconButton
                                                style={{ padding: "0px", color: "#22bb33" }}
                                                aria-label="Edit"
                                                onClick = {() => submitItem(category.id)}
                                              >
                                                <CheckCircle/>
                                              </IconButton>
                                            </GridItem>
                                          </GridContainer>
                                        )
                                      }
                                    })                                      
                                  )}
                                  <div className='menu-item-actions'>
                                    {!flag && (<button onClick={() => handleAddRemoveItem(category.id)}>Add Item</button>)}
                                    <button onClick={() => deleteAddon(category.id, index)}>Delete Addon</button>
                                  </div>
                                  </>
                                ))}
                              </Paper>
                                )
                              })}
                              </>
                            )}
                          {data.type === 'custom' && (
                            <Paper variant='outlined' style = {{padding: "8px"}}>
                              <GridContainer>
                                <GridItem xs = {12} sm = {12} md = {6}>
                                  <FormInput
                                    type="text"
                                    label="Addon Name"
                                    value = {addon.addon_category_name}
                                    onChange = {(e) => handleAddAddonChanges('addon_category_name', e.target.value, null)}
                                  />
                                </GridItem>
                                <GridItem xs = {12} sm = {12} md = {6}>
                                  <FormInput
                                    type="text"
                                    label="Addon Arabic Name"
                                    value = {addon.addon_category_name_arbic}
                                    onChange = {(e) => handleAddAddonChanges('addon_category_name_arbic', e.target.value, null)}
                                  />
                                </GridItem>
                              </GridContainer>
                              <GridContainer>
                                <GridItem xs = {12} sm = {12} md = {6}>
                                  <FormInput
                                    type="text"
                                    label="Max Limit"
                                    value = {addon.addon_category_max_limit}
                                    onChange = {(e) => handleAddAddonChanges('addon_category_max_limit', e.target.value, null)}
                                  />
                                </GridItem>
                                <GridItem xs={12} sm={12} md={6}>
                                  <FormControl style = {{width : "100%", marginTop : "8%"}}>
                                    <Select
                                      style = {{paddingTop : "14px"}}
                                      native
                                      value = {addon.addon_category_type}
                                      onChange = {(e) => handleAddAddonChanges('addon_category_type', e.target.value, null)}
                                    >
                                      <option style={{display : 'none'}} aria-label="None" value="">
                                        Select Addon Type
                                      </option>
                                      <option value="single">Single</option>
                                      <option value="multiple">Multi-Select</option>
                                    </Select>
                                  </FormControl>
                                </GridItem>
                              </GridContainer>
                              {addon.addon_item_name.map((item, index) => {
                                return (
                                    <GridContainer>
                                      <GridItem xs={12} sm={6} md={3}>
                                        <FormInput
                                          type="text"
                                          label="Item Name"
                                          value={addon.addon_item_name[index]}
                                          onChange = {(e) => handleAddAddonChanges('addon_item_name', e.target.value, index)}
                                        />
                                      </GridItem>
                                      <GridItem xs={12} sm={6} md={3}>
                                        <FormInput
                                          type="text"
                                          label="Item Name Arabic"
                                          value={addon.addon_item_name_arbic[index]}
                                          onChange = {(e) => handleAddAddonChanges('addon_item_name_arbic', e.target.value, index)}
                                        />
                                      </GridItem>
                                      <GridItem xs={12} sm={5} md={2}>
                                        <FormInput
                                          type="text"
                                          label="Item Price"
                                          value={addon.addon_item_price[index]}
                                          onChange = {(e) => handleAddAddonChanges('addon_item_price', e.target.value, index)}
                                        />
                                      </GridItem>
                                      <GridItem xs={12} sm={6} md={2}>
                                        <FormInput
                                          type="text"
                                          label="Item Calorie"
                                          value={addon.addon_item_calorie[index]}
                                          onChange = {(e) => handleAddAddonChanges('addon_item_calorie', e.target.value, index)}
                                        />
                                      </GridItem>
                                        {addon.addon_item_name.length > 1 && (
                                          <GridItem
                                            xs={12}
                                            sm={12}
                                            md={1}
                                            style={{ position: "relative", top: 50 }}
                                          >
                                            <IconButton
                                              style={{ padding: "0px", color: "#bb2124" }}
                                              aria-label="Edit"
                                              onClick = {() => handleAddonAddRemoveItem(false, index)}
                                            >
                                              <Delete
                                              />
                                            </IconButton>
                                          </GridItem>
                                        )}
                                    {index === addon.addon_item_name.length - 1 && (
                                      <GridItem
                                        xs={12}
                                        sm={12}
                                        md={1}
                                        style={{ position: "relative", top: 50 }}
                                      >
                                        <IconButton
                                          style={{ padding: "0px", color: "#22bb33" }}
                                          aria-label="Edit"
                                          onClick = {() => handleAddonAddRemoveItem(true, null)}
                                        >
                                          <AddCircle />
                                        </IconButton>
                                      </GridItem>
                                    )}
                                  </GridContainer>
                                )
                              })}

                              <GridContainer>
                                <GridItem xs = {12} sm = {12} md = {12}>
                                  <button onClick={() => submitAddon()} className='save-menu-btn' style={{margin : "16px auto 0px auto"}}>Add Addon</button>
                                </GridItem>
                              </GridContainer>
                            </Paper>
                          )}
                    </CardBody>
                </Card>
            </Modal>
        )
    } else {
        return <></>
    }
}
