import React, {useState, useEffect} from 'react';
import { Modal } from '@material-ui/core';
import { postData1, postDataAndImage } from 'components/FetchServices';
import CardBody from 'components/Card/CardBody';
import CardHeader from 'components/Card/CardHeader';
import Card from 'components/Card/Card'
import GridContainer from 'components/Grid/GridContainer';
import GridItem from 'components/Grid/GridItem';
import FormInput from 'components/FormInput/FormInput';
import { Paper, IconButton,FormControl, Select } from '@material-ui/core';
import { Delete, CheckCircle, AddCircle } from '@material-ui/icons';
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";



export default function ViewMenu({menuId, handleClose}) {
    const [data, setData] = useState({});

    useEffect(() => {
        fetchData();
    }, [])

    const fetchData = async() => {
        let res = await postData1('menu-detail', {menu_id :  menuId});
        if(res && res.data && res.status) {
            setData({...res.data});
        } else {
            handleClose();
        }
    }

    if(Object.keys(data).length > 0) {
        return (
            <Modal open style = {{display : "flex", alignItems : "center", justifyContent: "center"}}>
                <Card style = {{ maxWidth : "700px", marginTop : "3rem"}}>
                  <CardHeader color = "primary">
                    <GridContainer>
                      <GridItem xs={3} md={5}>
                        <IconButton style={{ padding: "0px" }} aria-label="Edit" onClick={() => handleClose()}>
                          <ArrowBackIosIcon
                          style = {{color : "#fff"}}
                          />
                        </IconButton>
                      </GridItem>
                      <GridItem xs={8} md={4}>
                        <h4 style={{margin : "0px"}}>View Menu</h4>
                      </GridItem>
                    </GridContainer>
                  </CardHeader>
                    <CardBody style = {{maxHeight : "calc(100vh - 7rem)", overflow : "auto"}}>
                        <GridContainer>
                            <GridItem xs={12} sm={12} md={6}>
                                <h4 style={{ fontWeight : "500", margin: "0px", fontSize: "18px"}}>Category: {data.menu_category[0].name}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                                <h4 style={{ fontWeight : "500", margin: "0px", fontSize: "18px"}}>Subcategory: {data.menu_sub_category[0].name}</h4>
                            </GridItem>
                        </GridContainer>
                        <GridContainer>
                          <GridItem xs={12} sm={6} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Name:</span> {data.name}</h4>
                          </GridItem>
                            <GridItem xs={12} sm={6} md={6}>
                                <h4 style={{ fontWeight :"300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Arabic Name:</span> {data.name_arbic}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Description:</span> {data.description}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={12} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Arabic Description:</span> {data.description_arbic}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={6} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Type:</span> {data.type}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={6} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Calorie:</span> {data.calorie}</h4>
                            </GridItem>
                            <GridItem xs={12} sm={6} md={6}>
                                <h4 style={{ fontWeight : "300", margin: "0px", fontSize: "14px"}}><span style={{fontWeight : "500"}}>Price:</span> {data.price}</h4>
                            </GridItem>
                        </GridContainer>
                          {(data.type === 'custom' && data.addon_category && data.addon_category.length > 0) && (
                              <>
                              <h4 style={{ fontWeight : "500", margin: "0px", marginBottom : "1rem", textDecoration : "underline", fontSize: "16px"}}>Addon Categories</h4>
                              {data.addon_category.map((category, index) => {
                                return (
                                <>
                                  <div className='menu-category'>
                                      <p>{index + 1}. {category.name} ({category.name_arbic})</p>
                                      <p>Max Limit: {category.max_limit}</p>
                                      <p>Type: {category.type}</p>
                                  </div>
                                {(data.addon_item && data.addon_item.length > 0 && (
                                  <>
                                  {/* <h4 style={{textAlign : "center", textDecoration: "underline", fontWeight : "900", margin: "0px", fontSize: "15px"}}>Items</h4> */}
                                  <table className='menu-edit-table'>
                                    <thead>
                                      <tr>
                                        <th>Name</th>
                                        <th>Arabic Name</th>
                                        <th>Price</th>
                                        <th>calorie</th>
                                      </tr>
                                    </thead>
                                    <tbody>
                                      {data.addon_item.map(item => {
                                        if(item.addon_category_id === category.id) {
                                          return (
                                            <tr>
                                              <td>{item.name}</td>
                                              <td>{item.name_arbic}</td>
                                              <td>{item.price}</td>
                                              <td>{item.calorie}</td>
                                            </tr>
                                          )
                                        }
                                      })}
                                    </tbody>
                                  </table>
                                  </>
                                ))}
                                </>
                                )
                              })}
                              </>
                            )}
                    </CardBody>
                </Card>
            </Modal>
        )
    } else {
        return <></>
    }
}
