import React, { useState } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  IconButton,
  FormHelperText,
} from "@material-ui/core";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
import AddButton from "components/CustomButtons/Button.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import CardFooter from "components/Card/CardFooter.js";
import FormInput from "components/FormInput/FormInput.js";
import FoodCategory from "views/FoodCategory/FoodCategory";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import { postData1 } from "components/FetchServices";

const styles = {
  cardCategoryWhite: {
    color: "rgba(255,255,255,.62)",
    margin: "0",
    fontSize: "14px",
    marginTop: "0",
    marginBottom: "0",
  },
  cardTitleWhite: {
    color: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
  },
  cardButtonWhite: {
    color: "black",
    backgroundColor: "#FFFFFF",
    marginTop: "0px",
    minHeight: "auto",
    fontWeight: "300",
    fontFamily: "'Roboto', 'Helvetica', 'Arial', sans-serif",
    marginBottom: "3px",
    textDecoration: "none",
    "&:hover": {
      backgroundColor: "#FFFFFF",
      color: "black",
    },
    "& small": {
      color: "#777",
      fontSize: "65%",
      fontWeight: "400",
      lineHeight: "1",
    },
  },
  underline: {
    "&:hover:not($disabled):before,&:before": {
      borderColor: "#AAAAAA" + " !important",
      borderWidth: "1px !important",
    },
    "&:after": {
      borderColor: "#972C26",
    },
  },
};

const useStyles = makeStyles(styles);

export default function AddSubcat() {
  const classes = useStyles();
  const [form, setForm] = useState(true);
  const [table, setTable] = useState(false);
  const [name, setName] = useState("");
  const [nameError, setNameError] = useState(false);
  const [nameArabic, setNameArabic] = useState("");
  const [nameArabicError, setNameArabicError] = useState(false);

  const changeView = () => {
    setTable(true);
    setForm(false);
  };

  const addCategory = async () => {
    if (name == "") {
      setNameError(true);
    }

    if (nameArabic == "") {
      setNameArabicError(true);
    }

    var body = {
      name: name,
      name_arbic: nameArabic,
      language: "en",
    };
    console.log("body:", body);
    let result = await postData1("category-add", body);
    console.log(result);
    if (result.status == true) {
      changeView();
      clearValue();
    } else {
      // alert('false')
    }
  };

  const clearValue = () => {
    setName("");
    setNameArabic("");
  };

  return (
    <div>
      <GridContainer>
        {form && (
          <GridItem xs={12} sm={12} md={8}>
            <Card>
              <CardHeader color="primary">
                <GridContainer>
                  <GridItem xs={3} md={5}>
                    <IconButton style={{ padding: "0px" }} aria-label="Edit">
                      <ArrowBackIosIcon
                        className={classes.cardTitleWhite}
                        onClick={changeView}
                      />
                    </IconButton>
                  </GridItem>
                  <GridItem xs={8} md={4}>
                    <h4 className={classes.cardTitleWhite}>Add Category</h4>
                  </GridItem>
                </GridContainer>
              </CardHeader>
              <CardBody>
                <GridContainer>
                  <GridItem xs={12} sm={12} md={6}>
                    <FormInput
                      type="text"
                      label="Name"
                      id="name"
                      value={name}
                      onChange={(event) => setName(event.target.value)}
                    />
                    {nameError && (
                      <FormHelperText error={nameError}>
                        Please enter the category name
                      </FormHelperText>
                    )}
                  </GridItem>
                  <GridItem xs={12} sm={12} md={6}>
                    <FormInput
                      type="text"
                      label="Arabic Name"
                      id="name_arabic"
                      value={nameArabic}
                      onChange={(event) => setNameArabic(event.target.value)}
                    />
                    {nameArabicError && (
                      <FormHelperText error={nameArabicError}>
                        Please enter the category name
                      </FormHelperText>
                    )}
                  </GridItem>
                </GridContainer>
                {/* <GridContainer>
                  <GridItem xs={12} sm={12} md={12}>
                    <TextField
                      style={{ marginTop: '6%' }}
                      className={classes.underline}
                      required
                      fullWidth
                      onChange={(event) => handlePicture(event)}
                      // onChange={(event)=>setPicture({icon:URL.createObjectURL(event.target.files[0]),file:event.target.files[0]})}
                      type="file"
                      autoComplete="current-password"

                    />
                  </GridItem>
                </GridContainer> */}
              </CardBody>
              <CardFooter>
                <AddButton color="primary" onClick={addCategory}>
                  Add
                </AddButton>
              </CardFooter>
            </Card>
          </GridItem>
        )}
        {table && (
          <GridItem xs={12} sm={12} md={12}>
            <FoodCategory />
          </GridItem>
        )}
      </GridContainer>
    </div>
  );
}
