import React, { useState, useEffect } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  TableCell,
  TableRow,
  TableBody,
  TableHead,
  Table,
  Avatar,
  CircularProgress,
} from "@material-ui/core";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
//import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import AddFoodCategory from "../FoodCategory/AddFoodCategory";
import AddToPhotosIcon from "@material-ui/icons/AddToPhotos";
import EditFoodCategory from "./EditFoodCategory";
import Image from "assets/img/food-2.jpg";
import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import IconButton from "@material-ui/core/IconButton";
import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";
import AddIcon from "@material-ui/icons/Add";
import { postData1 } from "components/FetchServices";

// const styles = {

// };

const useStyles = makeStyles(styles);

export default function FoodCategory(props) {
  const classes = useStyles();
  const { tableHeaderColor } = props;
  const [table, setTable] = useState(true);
  const [form, setForm] = useState(false);
  const [editableId, setEditableId] = useState();
  const [editForm, setEditForm] = useState(false);
  const [loading, setLoading] = useState(true);
  const [categoryList, setCategoryList] = useState([]);
  const addPage = () => {
    setForm(true);
    setTable(false);
  };

  const editPage = (id) => {
    setEditForm(true);
    setTable(false);
    setEditableId(id);
  };

  const getAllCategory = async () => {
    var body = {
      language: "en",
    };

    let result = await postData1("category-list", body);
    console.log(result);
    if (result.status == 1) {
      setCategoryList(result.data);
      setLoading(false);
    } else {
      setCategoryList([]);
    }
  };

  const deleteCategory = async (id) => {
    let body = {
      id: id,
    };

    let result = await postData1("category-delete", body);
    if (result.status == true) {
      setCategoryList(categoryList.filter((item) => item.id !== id));
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  return (
    <GridContainer>
      {table && (
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <GridContainer>
                <GridItem xs={8} sm={10} md={11}>
                  <h4 className={classes.cardTitleWhite}>Category</h4>
                </GridItem>
                <GridItem xs={4} sm={2} md={1}>
                  <Button
                    style={{
                      borderBottomLeftRadius: "16px",
                      borderTopLeftRadius: "16px",
                    }}
                    className={classes.cardButtonWhite}
                    startIcon={<AddIcon />}
                    onClick={addPage}
                  >
                    Add
                  </Button>
                </GridItem>
              </GridContainer>
            </CardHeader>
            <CardBody>
              {loading ? (
                <div className={classes.centerAlign}>
                  <CircularProgress color="inherit"/>
                </div>
              ) : (
                <div className={classes.tableResponsive}>
                  <Table className={classes.table}>
                    <TableHead
                      className={classes[tableHeaderColor + "TableHeader"]}
                    >
                      <TableRow className={classes.tableHeadRow}>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Image
                        </TableCell>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Category
                        </TableCell>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Action
                        </TableCell>
                      </TableRow>
                    </TableHead>
                    {categoryList.map((item) => {
                      return (
                        <TableBody>
                          <TableRow className={classes.tableBodyRow}>
                            <TableCell className={classes.tableCell}>
                              <Avatar alt="Remy Sharp" src={Image} />
                            </TableCell>
                            <TableCell className={classes.tableCell}>
                              {item.name}
                            </TableCell>
                            <TableCell className={classes.tableActions}>
                              <IconButton
                                aria-label="Edit"
                                className={classes.tableActionButton}
                                onClick={() => editPage(item.id)}
                              >
                                <Edit
                                  className={
                                    classes.tableActionButtonIcon +
                                    " " +
                                    classes.edit
                                  }
                                />
                              </IconButton>
                              <IconButton
                                aria-label="Close"
                                className={classes.tableActionButton}
                                onClick={() => deleteCategory(item.id)}
                              >
                                <Close
                                  className={`${classes.tableActionButton} ${classes.Close}`}
                                />
                              </IconButton>
                            </TableCell>
                          </TableRow>
                        </TableBody>
                      );
                    })}
                  </Table>
                </div>
              )}
            </CardBody>
          </Card>
        </GridItem>
      )}
      {form && (
        <GridItem xs={12} sm={12} md={12}>
          <AddFoodCategory />{" "}
        </GridItem>
      )}
      {editForm && (
        <GridItem xs={12} sm={12} md={12}>
          <EditFoodCategory id={editableId} />{" "}
        </GridItem>
      )}
    </GridContainer>
  );
}
