import React, { useEffect, useState } from "react";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
import {
  Button,
  TableCell,
  TableRow,
  TableBody,
  TableHead,
  Table,
  Avatar,
} from "@material-ui/core";
// core components
import GridItem from "components/Grid/GridItem.js";
import GridContainer from "components/Grid/GridContainer.js";
//import Table from "components/Table/Table.js";
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import AddMenu from "./AddMenu";
import AddIcon from "@material-ui/icons/Add";
import Image from "assets/img/food-2.jpg";
import Edit from "@material-ui/icons/Edit";
import Close from "@material-ui/icons/Close";
import IconButton from "@material-ui/core/IconButton";

import {
  defaultFont,
  primaryColor,
  dangerColor,
  grayColor,
} from "assets/jss/material-dashboard-react.js";

import styles from "assets/jss/material-dashboard-react/components/tableStyle.js";
import { postData1 } from "components/FetchServices";
import { ImageBaseURL } from "utils/common";
import ViewMenu from "./ViewMenu";
import EditMenu from "./EditMenu";
import VisibilityOutlined from "@material-ui/icons/VisibilityOutlined";

// const styles = {

// };

const useStyles = makeStyles(styles);

export default function TableList(props) {
  const classes = useStyles();
  const { tableHead, tableData, tableHeaderColor } = props;
  const [modal, setModal] = useState(false);
  const [modalMenuId, setModalMenuId] = useState(null);
  const [editModal, setEditModal] = useState(false);
  const [editId, setEditId] = useState(null);

  const [table, setTable] = React.useState(true);
  const [form, setForm] = React.useState(false);
  const changeView = () => {
    setForm(true);
    setTable(false);
  };

  const [rows, setRows] = React.useState([]);
  const readMenu = async () => {
    var body = {
      "language": "en"
    };
    let result = await postData1("menu-list", body);
    if (result.status == 1) {
      setRows(result.data);
    }
  };

  useEffect(() => {
    readMenu();
  }, []);

  const handleUpload = (isSuccess) => {
    if (isSuccess) {
      readMenu();
    }
    setTable(true);
    setForm(false);
  }

  const handleMenuModal = (menuId) => {
    if (modal) {
      setModal(false);
      setModalMenuId(null);
      readMenu();
    } else {
      setModal(true);
      setModalMenuId(menuId);
    }
  }

  const handleEditMenuModal = (menuId) => {
    if (editModal) {
      setEditModal(false);
      setEditId(null);
      readMenu();
    } else {
      setEditModal(true);
      setEditId(menuId);
    }
  };

  const handleMenuDelete = async(id) => {
    let res = await postData1('menu-delete', {menu_id : id});
    if(res && res.status) {
      readMenu();
    }
  }

  return (
    <>
    {editModal && (<EditMenu menuId = {editId} handleClose = {handleEditMenuModal}/>)}
    {modal && <ViewMenu menuId = {modalMenuId} handleClose = {handleMenuModal}/>}
    <GridContainer>
      {table && (
        <GridItem xs={12} sm={12} md={12}>
          <Card>
            <CardHeader color="primary">
              <GridContainer>
                <GridItem xs={8} sm={10} md={11}>
                  <h4 className={classes.cardTitleWhite}>Menu</h4>
                  {/* <p className={classes.cardCategoryWhite}>
              Categry
            </p> */}
                  </GridItem>
                  <GridItem xs={4} sm={2} md={1}>
                    <Button
                      style={{
                        borderBottomLeftRadius: "16px",
                        borderTopLeftRadius: "16px",
                      }}
                      className={classes.cardButtonWhite}
                      startIcon={<AddIcon />}
                      onClick={changeView}
                    >
                      Add
                    </Button>
                  </GridItem>
                </GridContainer>
              </CardHeader>
              <CardBody>
                {/* <Table
              tableHeaderColor="primary"
              tableHead={["Category","Image","Action"]}
              tableData={[
                ["South Indian"],
                ["Chinese"],
                ["North Indian"],
                ["Full Meal"],
               
              ]}
            /> */}
                <div className={classes.tableResponsive}>
                  <Table className={classes.table}>
                    <TableHead
                      className={classes[tableHeaderColor + "TableHeader"]}
                    >
                      <TableRow className={classes.tableHeadRow}>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Image
                        </TableCell>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Category
                        </TableCell>

                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          MenuItem
                        </TableCell>
                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Price
                        </TableCell>

                        <TableCell
                          className={
                            classes.tableCell + " " + classes.tableHeadCell
                          }
                        >
                          Action
                        </TableCell>
                      </TableRow>
                    </TableHead>

                    <TableBody>
                      {rows.map((item) => (
                        <TableRow className={classes.tableBodyRow}>
                          <TableCell className={classes.tableCell}>
                            <Avatar
                              alt="Remy Sharp"
                              src={item.image === null ? "" : `${ImageBaseURL}${item.image}`}
                            />
                          </TableCell>
                          {item.category.map((data) => (
                            <TableCell className={classes.tableCell}>
                              {data.name}
                            </TableCell>
                          ))}

                          <TableCell className={classes.tableCell}>
                            {item.name}
                          </TableCell>
                          <TableCell className={classes.tableCell}>
                            {item.price}
                          </TableCell>

                          <TableCell className={classes.tableActions}>
                            <IconButton
                              aria-label="Edit"
                              className={classes.tableActionButton}
                              onClick={() => handleEditMenuModal(item.id)}
                            >
                              <Edit
                                className={
                                  classes.tableActionButtonIcon +
                                  " " +
                                  classes.edit
                                }
                              />
                            </IconButton>
                            <IconButton
                              aria-label="Close"
                              className={classes.tableActionButton}
                              onClick={() => handleMenuDelete(item.id)}
                            >
                              <Close
                                className={
                                  classes.tableActionButtonIcon +
                                  " " +
                                  classes.close
                                }
                              />
                            </IconButton>
                            <IconButton
                              aria-label="Close"
                              className={classes.tableActionButton}
                              onClick={() => handleMenuModal(item.id)}
                            >
                              <VisibilityOutlined />
                            </IconButton>
                          </TableCell>
                        </TableRow>
                      ))}
                    </TableBody>
                  </Table>
                </div>
              </CardBody>
            </Card>
          </GridItem>
        )}
        {form && (
          <GridItem xs={12} sm={12} md={12}>
            <AddMenu handleUpload={handleUpload} />
          </GridItem>
        )}
      </GridContainer>
    </>
  );
}
