import React from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';

import { Grid, Paper, Link, TextField } from '@material-ui/core';
import Box from '@material-ui/core/Box';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import CustomInput from "components/CustomInput/CustomInput.js";
import FormInput from "components/FormInput/FormInput.js";

import bgImage from "assets/img/login.jpg";
import { useHistory } from "react-router-dom";
import logo from "assets/img/logo.png";
import { postData } from 'components/FetchServices';


const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  logo: {
    width: '12%',
    marginBottom: '10%'
  },
  title: {
    // fontFamily:'Poppins-SemiBold',
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding: '20px'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: '#972C26'
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    background: '#972C26',
    '&:hover': {
      backgroundColor: '#972C26',
      fontStyle: "Poppins-Bold"
    },
  },

}));

export default function SignIn(props) {
  const classes = useStyles();
  const history = useHistory();

  const [email, setEmail] = React.useState('')
  const [password, setPassword] = React.useState('')


  const handleLogin = async (e) => {
    e.preventDefault();
    var body = {
      'email': email,
      'password': password,
    }
    let result = await postData('login', body)
    console.log(result)

    if (result.status == true) {
      localStorage.setItem('LOGIN', JSON.stringify(result.data[0]))
      history.push("/admin");
    } else {
      alert('Invalid Id/Password')
    }
  }
  const forgotPassword = () => {
    history.push("/fogot_password");
  }

  return (
    <Grid container component="main" className={classes.root}>
      <CssBaseline />
      <Grid item md={12} sm={12} xs={12} style={{ backgroundImage: "url(" + bgImage + ")" , backgroundSize: "cover"}}>
        <Container component="main" maxWidth="xs">

          <Paper elevation={6} >
            <div className={classes.paper}>
              <img src={logo} className={classes.logo} />

              {/* <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar> */}
              <Typography component="h1" variant="h5" className={classes.title}>
                Log In to Merchant Panel
              </Typography>
              <form className={classes.form} noValidate>

                <FormInput
                  type="email"
                  label="Email"
                  id="email"
                  value={email}
                  onChange={(event) => setEmail(event.target.value)} />

                <FormInput
                  type="password"
                  label="Password"
                  id="password"
                  value={password}
                  onChange={(event) => setPassword(event.target.value)} />

                <Button
                  type="submit"
                  onClick={(e) => handleLogin(e)}
                  fullWidth
                  variant="contained"
                  color="primary"
                  className={classes.submit}
                >
                  Log In
                </Button>
                <Grid container>
                  <Grid item xs>
                    <Link onClick={forgotPassword} variant="body2" style={{ color: '#972C26' }}>
                      Forgot password?
                    </Link>
                  </Grid>
                  {/* <Grid item>
              <Link href="#" variant="body2">
                {"Don't have an account? Sign Up"}
              </Link>
            </Grid> */}
                </Grid>
              </form>

            </div>
          </Paper>


        </Container>
      </Grid>
    </Grid>
  );
}