import logo from "assets/img/logo.png";
import { postData } from 'components/FetchServices';


const useStyles = makeStyles((theme) => ({
  root: {
    height: '100vh',
  },
  logo:{
    width:'12%',
    marginBottom:'10%'
  },
  title:{
   // fontFamily:'Poppins-SemiBold',
   marginTop:'5%'
  },
  paper: {
    marginTop: theme.spacing(8),
    display: 'flex',
    flexDirection: 'column',
    alignItems: 'center',
    padding:'20px'
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: '#972C26'
  },
  form: {
    width: '100%', // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    background:'#972C26',
    '&:hover': {
      backgroundColor:'#972C26',
      fontStyle:"Poppins-Bold"
      },
  },

}));

export default function ForgotPassword(props) {
  const classes = useStyles();
  const history = useHistory();

  const [email,setEmail] = React.useState('')

  const forgotPassword=async()=> {
  
    var body ={
      'email':email,
    }
     let result = await postData('users/forgotPassword',body)
     console.log(result)

     if(result.status==1){
       alert(true)
      history.push("/");
     }else{
      alert('Failed to Reset Password')
     }
}
 

  return (
    <Grid container component="main" className={classes.root}>
       <CssBaseline />
    <Grid item md={12} sm={12} xs={12} style={{ backgroundImage: "url(" + bgImage + ")" }}>
    <Container component="main" maxWidth="xs">
     
      <Paper elevation={6} >
      <div className={classes.paper}>
        {/* <img src={logo} className={classes.logo}/> */}
       
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5" className={classes.title}>
          Reset Password
        </Typography>
        <form className={classes.form} noValidate>
 
             <FormInput
                    type="email"
                    label="Email"
                    id="email"
                    value={email}
                    onChange={(event)=>setEmail(event.target.value)}  />

          <Button
            // type="submit"
            onClick={forgotPassword}
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
           Reset
          </Button>
         
        </form>
        
      </div>
      </Paper>
      
   
    </Container>
    </Grid>
    </Grid>
  );
}